@import "colors";

.contact{
    height: 100vh;
    width: 100%;
    background-image: url("../assets/background1.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;

    > form{
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: white;
        padding: 2rem;
        width: 40%;
        height: 80%;
        justify-content: center;

        h2{
            font-size: 1.5rem;
            text-transform: uppercase;
            font-family: "Roboto";
            margin: 2rem 0;
        }

        >input , >textarea , >button{
            padding: 1rem;
            outline: none;
            border: 1px solid rgb(202, 202 , 202);
            margin: 0.5rem;
            width: 60%;
            border-radius: 1rem;
        }

        >button{
            background-color: $red;
            border: none;
            cursor: pointer;
            color: wheat;
            font-size: 1rem;

            &:hover{
                background-color: $reddark;
            }
        }
    }

    .formBorder{

        width: 200px;
        height: 80%;
        background-color: $red;
        border-radius: 0 200px 200px 0;
        position: relative;
        > div{
            position: absolute;
            top: 50%;
            transform: translate(50% , -50%);
            filter: drop-shadow(0 0 10px rgba(0,0,0,0.5));

            > img{
                width: 100%;
            }
        }

    }
}

@media screen and (max-width:600px) {
    .contact{
        height: 140vh;

        > form{
            width: 100%;
            height: 100%;
        }

    .formBorder{
        display: none;
    }
    }
}